import { createApp } from 'vue';
import App from './App.vue';
import router from './router';
import store from './store';
import { Button, Icon, Grid, GridItem, Popup, ImagePreview } from 'vant';
import Varlet from '@varlet/ui';
import '@varlet/ui/es/style';

createApp(App)
  .use(store)
  .use(router)
  .use(Varlet)
  .use(Button)
  .use(Icon)
  .use(Grid)
  .use(Popup)
  .use(GridItem)
  .use(ImagePreview)
  .mount('#app');
